import { i18nMark } from '@lingui/react'
import React from 'react'

import { OrderStateEnum } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { TOrderStateModel } from '../models/OrderStateModel'

interface IProps {
  orderId: ID
  nextState: OrderStateEnum
  stateModel: TOrderStateModel
}

const stateLabels = {
  [OrderStateEnum.ACCEPTED]: i18nMark('Accept order'),
  [OrderStateEnum.FINISHED]: i18nMark('Finish order'),
}

export const OrderStateButton: React.SFC<IProps> = ({
  orderId,
  nextState,
  stateModel,
}) => (
  <TouchButton
    text={i18n._(stateLabels[nextState])}
    width="7.375rem"
    onClick={() => {
      stateModel.updateOrderState(orderId, nextState)
    }}
  />
)
