import React from 'react'
import { FieldType, TextArea } from 'react-form'
import styled from 'styled-components'

import { Radiuses, Spacing } from '../styling'
import { FormInputStyled } from './FormInputTextBox'

interface IProps extends React.InputHTMLAttributes<never> {
  field: FieldType
  defaultValue?: string
  inputWidth?: number
  inputWidthUnit?: string
}

const FormInputTextBoxMultilineStyled = styled(TextArea)`
  padding: 0.5rem;
  border-radius: ${Radiuses.TextComponent};
  outline: none;
  resize: none;
  overflow: auto;
  margin: ${Spacing.MarginAroundFormComponents};
  width: ${({ width }) => width};
`

export const FormInputTextBoxMultiline: React.SFC<IProps> = ({
  field,
  defaultValue,
  inputWidth = Spacing.DefaultInputWidth,
  inputWidthUnit = Spacing.DefaultSizeUnit,
  children,
  ...props
}) =>
  field ? (
    <FormInputTextBoxMultilineStyled
      field={field}
      defaultValue={defaultValue}
      width={`${inputWidth}${inputWidthUnit}`}
      {...props}
    />
  ) : (
    <FormInputStyled {...props} />
  )
