import React from 'react'

import { formatPrice } from '../helpers/formats'

interface IPriceProps {
  price: number
  currencyCode?: string
}

export const Price: React.SFC<IPriceProps> = ({ price, currencyCode }) => (
  <span>
    {currencyCode
      ? `${formatPrice(price)} ${currencyCode}`
      : formatPrice(price)}
  </span>
)
