import React from 'react'

import { CardListTitle } from '../../../atoms/CardListTitle'
import { GOrderList } from '../../../graph/types'
import { OrderBox } from '../atoms/OrderBox'
import { TOrderListModel } from '../models/OrderListModel'

interface IProps {
  category: GOrderList.OrderStateCategories
  orders: GOrderList.Orders[]
  listModel: TOrderListModel
}

export const OrderListCategory: React.SFC<IProps> = ({
  orders,
  category,
  listModel,
}) =>
  orders.length > 0 ? (
    <>
      <CardListTitle>{category.code}</CardListTitle>
      {orders.map(order => (
        <OrderBox
          key={order.id}
          order={order}
          listModel={listModel}
          showBranch={listModel.branches.length > 1}
        />
      ))}
    </>
  ) : null
