import { differenceInMinutes } from 'date-fns'
import React from 'react'

import { TimeBadge } from '../../../atoms/TimeBadge'
import { DateTime } from '../../../graph/types'
import { useTimer } from '../../../hooks/useTimer'

interface IDeliveryTimeBadgeProps {
  deliverAt: DateTime
  deliveredAt: DateTime | null
  isCancelled?: boolean
  onClick?(): void
}

const MINUTE = 60 * 1000

export const DeliveryTimeBadge: React.SFC<IDeliveryTimeBadgeProps> = ({
  deliverAt,
  deliveredAt,
  isCancelled = false,
  onClick,
}) => {
  const now = useTimer({ interval: MINUTE, enabled: !deliveredAt })
  const minutes = differenceInMinutes(deliverAt, deliveredAt || now)
  return (
    <TimeBadge
      minutes={minutes}
      onClick={onClick}
      isFinished={Boolean(deliveredAt)}
      isCancelled={isCancelled}
    />
  )
}
