import raf from 'raf'
import { useEffect, useRef } from 'react'

import { useStateRef } from './useStateRef'

interface IOptions {
  interval: number
  enabled: boolean
  getNow?(): number
  nextTick?(onTick: () => void): number
  cancelTick?(handle: number): void
}

export const useTimer = ({
  interval,
  enabled,
  getNow = Date.now,
  nextTick = raf,
  cancelTick = raf.cancel,
}: IOptions) => {
  const [getLastUpdate, setLastUpdate] = useStateRef(getNow)
  const handleRef = useRef<number | null>(null)

  useEffect(
    () => {
      const onFrame = () => {
        const now = getNow()
        const last = getLastUpdate()
        const delta = now - last
        if (delta >= interval) {
          setLastUpdate(now)
        }
        loop()
      }

      const onStart = () => {
        loop()
      }

      const onStop = () => {
        if (handleRef.current) {
          cancelTick(handleRef.current)
          handleRef.current = null
        }
      }

      const loop = () => {
        if (enabled) {
          handleRef.current = nextTick(onFrame)
        } else {
          onStop()
        }
      }

      onStart()
      return onStop
    },
    [interval, enabled],
  )

  return getLastUpdate()
}
