import React from 'react'

import { BaseMap } from '../../../atoms/BaseMap'
import { WithRoot } from '../../../atoms/WithRoot'
import { OrderStateCategoryEnum } from '../../../graph/types'
import { OrderMapMarker } from '../molecules/OrderMapMarker'
import { getOrderState, ILoaderProps } from './OrderListLoader'

interface IProps extends ILoaderProps {}

const categoriesOnMap = [
  OrderStateCategoryEnum.NEW_ORDERS,
  OrderStateCategoryEnum.PREPARING,
  OrderStateCategoryEnum.ON_WAY,
]

export const OrderMap: React.SFC<IProps> = ({
  isLoading,
  noFilter,
  orders,
}) => (
  <WithRoot
    render={({ order: orderModel }) => (
      <BaseMap
        model={orderModel.map}
        render={() =>
          isLoading || noFilter
            ? null
            : orders!
                .filter(order => categoriesOnMap.includes(getOrderState(order)))
                .map(order => (
                  <OrderMapMarker
                    key={order.id}
                    order={order}
                    onClick={() => orderModel.list.focusOrderDetail(order.id)}
                  />
                ))
        }
      />
    )}
  />
)
