import { autorun } from 'mobx'
import { useObservableEffect, useObserver } from 'mobx-react-lite'
import React, { useEffect } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import { GUpdateOrder } from '../../../graph/types'
import { useGuardedMutation } from '../../../hooks/useGuardedMutation'
import { UpdateOrderMutation } from '../models/CreateOrderModel.graphql'

interface IProps {
  callingPhone?: string
  callingEventId: MaybeID
}

export const OrderFormSetup: React.FC<IProps> = ({
  callingPhone,
  callingEventId,
  children,
}) => {
  const { createOrder } = useRoot()

  const updateOrder = useGuardedMutation<{}, GUpdateOrder.Variables>(
    UpdateOrderMutation,
  )

  // update order whenever these would change
  useObservableEffect(() =>
    autorun(() => {
      if (createOrder.orderId) {
        updateOrder({
          orderId: createOrder.orderId,
          deliveryType: createOrder.deliveryType,
          gps: createOrder.gps,
        })
      }
    }),
  )

  // store calling phone number and possible eventId to model upon change
  useEffect(
    () => {
      if (callingPhone) {
        createOrder.userIsCalling(callingPhone, callingEventId)
      }
    },
    [callingPhone, callingEventId],
  )

  // model controls values of the form, observer would rerender on any change
  return useObserver(() => (
    <Form
      values={createOrder.formValues}
      onSubmit={createOrder.acceptOrder}
      onChange={formState =>
        createOrder.updateFormValues(formState.values, formState.touched)
      }
    >
      {children}
    </Form>
  ))
}
