import { Trans } from '@lingui/react'
import { Dialog, DialogTitle } from '@material-ui/core'
import React from 'react'
import styled from 'styled-components'

import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { TOrderStateModel } from '../models/OrderStateModel'

interface IProps {
  orderId: ID
  stateModel: TOrderStateModel
}

interface IState {
  dialogOpen: boolean
}

const minutesOption = [20, 30, 45, 60, 75, 90, 105, 120]

const MinutesContainer = styled.div`
  display: flex;
  justify-content: 'center';
  flex-wrap: 'wrap';
  font-size: 2rem;
`
const MinutesItem = styled.div`
  margin: 1rem 1rem 0 0;
`

export class OrderStateTimeNotification extends React.Component<
  IProps,
  IState
> {
  state = { dialogOpen: false }
  openDialog = () => {
    this.setState({ dialogOpen: true })
  }
  closeDialog = () => {
    this.setState({ dialogOpen: false })
  }
  render() {
    const { orderId, stateModel } = this.props
    return (
      <>
        <TouchButton iconName="schedule" onClick={this.openDialog} />
        <Dialog open={this.state.dialogOpen} onClose={this.closeDialog}>
          <DialogTitle>
            <Trans>
              Send expected time of delivery to customer (in minutes)
            </Trans>
            <MinutesContainer>
              {minutesOption.map(minutes => (
                <MinutesItem key={minutes}>
                  <TouchButton
                    onClick={() => {
                      stateModel.sendTimeNotification(orderId, minutes)
                      this.closeDialog()
                    }}
                    color={Colors.green}
                  >
                    {minutes}
                  </TouchButton>
                </MinutesItem>
              ))}
            </MinutesContainer>
          </DialogTitle>
        </Dialog>
      </>
    )
  }
}
