import gql from 'graphql-tag'

import { buildQuery } from '../../../graph/buildQuery'
import { GOrderMenu } from '../../../graph/types'

export const OrderMenuRecipe = gql`
  fragment GOrderMenuRecipe on Recipe {
    id
    nameLabel
    descriptionLabel
    image
    price(warePriceType: DELIVERY) {
      value
      currency {
        code
      }
    }
    sidedishCount: sideDishesWareCategoriesCount
  }
`

export const OrderMenuCategory = gql`
  fragment GOrderMenuCategory on WareCategory {
    id
    nameLabel
    pictogram
    recipes(enabled: true, isAvailable: true) {
      ...GOrderMenuRecipe
    }
  }
  ${OrderMenuRecipe}
`

const OrderMenuQuery = gql`
  query GOrderMenu($branchId: ID!) {
    orderMenu: wareCategories(
      wareCategoryType: MAIN
      companyBranch: $branchId
    ) {
      ...GOrderMenuCategory
    }
  }
  ${OrderMenuCategory}
  ${OrderMenuRecipe}
`

export const QueryOrderMenu = buildQuery<
  GOrderMenu.Query,
  GOrderMenu.Variables
>(OrderMenuQuery)
