import { Observer } from 'mobx-react'
import React from 'react'
import { FormApi } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'
import useRouterContext from '../../../hooks/useRouterContext'
import { useSnackBar } from '../../../hooks/useSnackBar'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'

export const OrderAcceptButton = () => {
  const { enqueueSuccess, enqueueWarning } = useSnackBar()
  const { history } = useRouterContext()
  const { createOrder } = useRoot()
  return (
    <FormApi>
      {formApi => (
        <Observer>
          {() => (
            <TouchButton
              color={Colors.green}
              text={i18n.t`Accept order`}
              disabled={!createOrder.canSubmit}
              onClick={async ev => {
                try {
                  await formApi.submitForm(ev)
                  history.replace('/order')
                  enqueueSuccess(i18n.t`Order has been accepted`)
                } catch (err) {
                  if (createOrder.hasSubmitErrors) {
                    createOrder.submitErrors!.forEach(error => {
                      enqueueWarning(error)
                    })
                  }
                }
              }}
            />
          )}
        </Observer>
      )}
    </FormApi>
  )
}
