import { WareList } from 'c2e-tools'
import { css } from 'emotion'
import React from 'react'
import { AutoSizer, List } from 'react-virtualized'
import styled from 'styled-components'

import { Card } from '../../../atoms/Card'
import {
  GOrderMenu,
  GOrderMenuCategory,
  GOrderMenuRecipe,
} from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { OrderMenuRecipe } from '../atoms/OrderMenuRecipe'

interface IProps {
  orderMenu: ReadonlyArray<GOrderMenu.OrderMenu>
  scrollToCategory: MaybeID
  onRecipeClick(recipe: GOrderMenuCategory.Recipes): void
}

const FullCard = styled(Card)`
  height: 100%;
`

const noOutline = css`
  outline: none;
`

export const OrderMenuList: React.SFC<IProps> = ({
  onRecipeClick,
  scrollToCategory,
  orderMenu,
}) => (
  <FullCard>
    <WareList<GOrderMenuRecipe.Fragment>
      recipeCategories={orderMenu}
      formatRecipe={recipe => <OrderMenuRecipe recipe={recipe} />}
      formatCategory={category => <b>{category.nameLabel}</b>}
      onRecipeClick={onRecipeClick}
    >
      {({ listItems, rowRenderer }) => (
        <AutoSizer>
          {({ width, height }) => (
            <List
              className={noOutline}
              height={height - 130}
              overscanRowCount={2}
              rowCount={listItems.length}
              rowHeight={({ index }) => (listItems[index].recipe ? 70 : 40)}
              rowRenderer={rowRenderer}
              scrollToAlignment="start"
              scrollToIndex={
                scrollToCategory
                  ? listItems.findIndex(
                      RM.pathEq(['category', 'id'], scrollToCategory),
                    )
                  : undefined
              }
              width={width}
            />
          )}
        </AutoSizer>
      )}
    </WareList>
  </FullCard>
)
