import React from 'react'
import styled from 'styled-components'

import { Price } from '../../../atoms/Price'
import { GOrderMenuRecipe } from '../../../graph/types'
import { Colors } from '../../../styling'

interface IProps {
  recipe: GOrderMenuRecipe.Fragment
}

const RecipeStyled = styled.div`
  position: relative;
  vertical-align: middle;
  cursor: pointer;
  padding: 0.2rem 0.5rem 1rem 0.3rem;
  border-bottom: 1px solid ${Colors.greyLight};
  &:hover {
    background-color: ${Colors.yellow};
  }
`

const RecipeImage = styled.img`
  width: 4rem;
  height: 4rem;
  display: block;
  float: left;
  object-fit: cover;
  margin: -1rem 1rem 0 0;
`

const RecipePrice = styled.div`
  position: absolute;
  right: 0.1rem;
  width: 4.8rem;
  height: 1.8rem;
  padding: 0.12rem 0.7rem 0.3rem 0rem;
  font-weight: 500;
  font-size: 1.1rem;
  color: ${Colors.greyDarkest};
  text-align: right;
  overflow: hidden;
  white-space: nowrap;
`

const RecipeDescription = styled.div`
  color: ${Colors.greyMiddle};
  font-size: 0.8rem;
  min-height: 1rem;
`

export const OrderMenuRecipe: React.SFC<IProps> = ({ recipe }) => {
  return (
    <RecipeStyled>
      {recipe.image && (
        <RecipeImage src={recipe.image} alt={recipe.nameLabel} />
      )}
      <RecipePrice>
        <Price
          price={recipe.price.value}
          currencyCode={recipe.price.currency.code}
        />
      </RecipePrice>
      <div>{recipe.nameLabel}</div>
      <RecipeDescription>{recipe.descriptionLabel || ' '}</RecipeDescription>
    </RecipeStyled>
  )
}
