import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { i18n } from '../../../i18n'
import { CompanyBranchPicker } from '../../../organisms/CompanyBranchPicker'

interface IProps {}

export const OrderFormBranch: React.SFC<IProps> = props => (
  <WithRoot
    render={({ user, createOrder }) => (
      <CompanyBranchPicker
        isClearable={false}
        selectedBranches={user.currentBranchId ? [user.currentBranchId] : []}
        onSelectionChange={([branchId]) => {
          if (
            !createOrder.orderId ||
            window.confirm(
              i18n.t`Changing branch will cancel current order and remove all items from the cart. Proceed?`,
            )
          ) {
            user.selectBranch(branchId)
            createOrder.resetOrder()
          }
        }}
      />
    )}
  />
)
