import { Observer } from 'mobx-react'
import React, { Component } from 'react'
import { GoogleMap, GoogleMapProps, withGoogleMap } from 'react-google-maps'
import styled from 'styled-components'

import { TMapModel } from '../models/MapModel'
import { Radiuses } from '../styling'

interface IBaseMapProps extends GoogleMapProps {
  model: TMapModel
  render(control: IMapControl): ReactNode | ReactNode[]
}

interface IMapControl {}

const MapContainerStyled = styled.div`
  border-radius: ${Radiuses.Card};
  overflow: hidden;
  height: 100%;
`

const BaseMapContainer = withGoogleMap(({ children }) => <>{children}</>)

export class BaseMap extends Component<IBaseMapProps> {
  getOptions = () => {
    return {
      streetViewControl: false,
      mapTypeControl: false,
      fullscreenControl: false,
    }
  }
  render() {
    const { model } = this.props
    return (
      <BaseMapContainer
        containerElement={<MapContainerStyled />}
        mapElement={<div style={{ height: `100%` }} />}
      >
        <Observer>
          {() => (
            <GoogleMap
              ref={model.setMapRef}
              zoom={model.zoom}
              center={model.center}
              onCenterChanged={model.updatePosition}
              onZoomChanged={model.updateZoom}
              options={this.getOptions()}
            >
              {this.props.render({})}
            </GoogleMap>
          )}
        </Observer>
      </BaseMapContainer>
    )
  }
}
