import { CancelOutlined } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'

import { Colors } from '../styling'

interface ITimeBadgeProps {
  minutes?: number
  onClick?(): void
  isFinished?: boolean
  isCancelled?: boolean
}

const TimeBadgeStyled = styled.div`
  min-width: 1.5rem;
  min-height: 1.5rem;
  padding: 0.3rem;
  border: 1px solid white;
  border-radius: 4rem;
  color: ${Colors.white};
  background: ${Colors.greyMiddle};
  text-align: center;
  font-size: 1rem;
  margin: 0;
  vertical-align: middle;
  font-family: 'Open Sans', sans-serif;
`

const TimeBadgeUnknown = styled(TimeBadgeStyled)`
  background: ${Colors.greyMiddle};
  color: ${Colors.greyDarkest};
`
const TimeBadgeInProgress = styled(TimeBadgeStyled)`
  background: ${Colors.orange};
`
const TimeBadgeBigTime = styled(TimeBadgeStyled)`
  font-size: 0.8rem;
  padding: 0.4rem 0.3rem 0.2rem 0.3rem;
  background: ${Colors.orange};
`
const TimeBadgeWarning = styled(TimeBadgeStyled)`
  background: ${Colors.red};
`
const TimeBadgeCompleted = styled(TimeBadgeStyled)`
  background: ${Colors.green};
`
const TimeBadgeCancelled = styled(TimeBadgeStyled)`
  background: ${Colors.greyMiddle};
  padding: 0.3rem 0.3rem 0 0.3rem;
`

export const TimeBadge: React.SFC<ITimeBadgeProps> = ({
  minutes = 0,
  onClick,
  isFinished = false,
  isCancelled = false,
}) => {
  if (isCancelled) {
    return (
      <TimeBadgeCancelled onClick={onClick}>
        <CancelOutlined />
      </TimeBadgeCancelled>
    )
  }

  let children = minutes.toString()
  let container = TimeBadgeInProgress

  if (Number.isNaN(minutes)) {
    container = TimeBadgeUnknown
    children = '?'
  } else if (isFinished) {
    if (minutes <= 0) {
      container = TimeBadgeWarning
    } else {
      container = TimeBadgeCompleted
    }
  } else if (minutes <= 20) {
    container = TimeBadgeWarning
    if (minutes < -99) {
      children = '-'
    }
  } else if (minutes > 99) {
    container = TimeBadgeBigTime
  }

  return React.createElement(container, { children, onClick })
}
